export default{
	
	data(){
		return {
			list:[],
			value:"",
			value2:"请搜索你的商品"
		}
	},
	methods:{
		InputFocus(e) {
			this.InputBottom = e.detail.height	
		},
		InputBlur(e) {
			this.InputBottom = 0
			this.value = e.detail.value
			//仅微信端调用 不加的话 手机端 会重复搜索

				this.Enter()

		},
		Returns(){
			uni.reLaunch({
			    url:"/pages/index/index"
			});
		},
		//因为微信小程序 有个胶囊阻碍 故加个键盘事件
		Enter(){
			this.enterseach()
		},
		//封装个方法 同时使用
		enterseach(){
			//如果输入的内容为空就会走这里
			if(this.value==""){
				return this.value2="搜索内容不能为空"
			}
			//如果文本不为空就往下执行
			this.list.unshift(this.value)
			if(this.list.length==8){
				let arr = this.list.pop()
				this.list = this.list
			}
			uni.navigateTo({
				url:`/pages/Sortinglist/Sortinglist?value=${this.value}`
			})
		},
		seachshijian(){
			this.enterseach()
		},
		delectrecord(){
			this.list=[]
			this.value=""
		},
		assignment(item){
			this.value=item;
		}
	},
	props:["value3"]
}
